import { BrowserRouter, Route, Routes } from "react-router-dom";
import App from "./App";
import OneArticle from './OneArticle'
import './style.css'

function RouteSwitch () {
    return (
        <BrowserRouter>
            <Routes>
                <Route path="/" element={<App/>}/>
                <Route path="/article/:id" element={<OneArticle/>}/>                
            </Routes>
        </BrowserRouter>
    )
}

export default RouteSwitch